import React, { useState } from "react";
import reactDOM from "react-dom";
import css from "./index.css"

function App() {

    const [Data, setData] = useState([])
    React.useEffect(() => {
        const userData = localStorage.getItem('Data')
        if (userData) {
            setData(JSON.parse(userData))
        }
    }, [])

    React.useEffect(() => {
        localStorage.setItem('Data', JSON.stringify(Data))
    })
    function TodoApp(e) {
        e.preventDefault()

        var todoText = document.getElementById('TodoText').value
        if (todoText === "") {
            alert("Please Write Some Text ")
        }

        setData((previouseVlaue) => {
            return previouseVlaue.concat(todoText)

        })

    }

    const remove = (index) => {
        let todo = [...Data]
        todo.splice(index, 1)
        setData(todo)
    };

    return (
        <div className="main-div">
        <div className="todoList">
            <form onSubmit={TodoApp} className="form">
                <center><h1>ToDo App</h1></center>
                <input type="text" id="TodoText" placeholder="Add items" /><button > + </button>
            </form>

            {Data.map((value, index) => {

                return (
                    <div className="flex" key={index} >
                        <p className="listText">{value}</p><button className=" button-del" onClick={() => remove(index)}> Remove </button>

                    </div>
                )
            })}

        </div></div>
    )
}
reactDOM.render(<App />, document.getElementById('root'))